body {
    margin: 0;
    overflow: hidden;
    font-family: Arial, sans-serif;
  }
  
  .app-container {
    display: flex;
    flex-direction: column;
    height: 100vh;
  }
  
  .header {
    background: #007bff;
    color: white;
    padding: 10px;
    text-align: center;
  }
  
  .main-content {
    display: flex;
    flex: 1;
  }
  
  .footer {
    background: rgba(255, 255, 255, 0.9);
    padding: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    display: flex;
    flex-direction: column;
    gap: 10px;
    z-index: 10;
  }
  
  .toolbar {
    display: flex;
    justify-content: space-around;
    gap: 10px;
  }
  
  .toolbar-button {
    background: #007bff;
    color: white;
    border: none;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
    transition: background 0.3s ease;
    font-size: 14px;
  }
  
  .toolbar-button:hover {
    background: #0056b3;
  }
  
  .image-selector {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-top: 10px;
  }
  
  .image-thumbnail {
    width: 60px;
    height: 60px;
    cursor: pointer;
    border: 2px solid transparent;
    border-radius: 5px;
    transition: border 0.3s ease, transform 0.3s ease;
  }
  
  .image-thumbnail.selected {
    border: 2px solid #007bff;
    transform: scale(1.1);
  }
  
  canvas {
    display: block;
    flex: 1;
  }
  
.toolbar-button:hover {
  background: #0056b3;
}

